<template>
  <div class="render">
    <div class="render__header">
      {{component.title}}
    </div>
    <div class="render__content">
      {{component.content}}

      <template v-if="component.containerType > -1">
        <ioc-container-drop
          :pid="component.id"
          :group="`container${component.containerType}`"
          :can-drop="canDrop"
        />
      </template>

    </div>
  </div>
</template>
<script>

import IocContainerDrop from '@/packages/canvas/container-drop'
export default {
  name: 'screen-render',

  components: {
    IocContainerDrop
  },

  props: {
    component: {
      type: Object,
      require: true
    }
  },

  methods: {
    canDrop (group, component) {
      // group 
      // component 
      const { containerType = -1 } = component
      // 1
      if (group === 'container1') {
        return true
      }
      // 2231
      if (group === 'container2') {
        return containerType !== 1
      }
      // 3231
      if (group === 'container3') {
        return containerType === -1
      }
      return true
    }
  }
}
</script>

